import React, { useEffect, useState } from 'react';
import Item from '../Item/Item';
import Loading from '../Loading/Loading';
import { Offline } from "react-detect-offline";
import Disconnected from '../Disconnected/Disconnected';
import { getTrending } from '../../Api';

export default function Home() {

  const [Movies , setMovies] = useState([]);
  const [Tv , setTv] = useState([]);
  const [People , setPeople] = useState([]);

  async function getData() {
    let movies = await getTrending('movie');
    setMovies(movies);

    let tv = await getTrending('tv');
    setTv(tv);

    let people = await getTrending('person');
    setPeople(people);
  };

  useEffect(() => {
    getData();
  }, []);
  
  
  return (<>
    <Offline><Disconnected/></Offline>
    {Movies.length>0?<>
      <div className="container">
        <div className="row">
          <div className="col-md-4">
            <div className="brdr w-25"></div>
            <h1>Trending <br /> Movies <br /> To Watch Now</h1>
            <p>Most watched movies by days</p>
            <div className="brdr w-100"></div>
          </div>
          {Movies.map((movie,index)=><Item key={index} data={movie}/>)}
        </div>
        <div className="row my-3">
          <div className="col-md-4">
            <div className="brdr w-25"></div>
            <h1>Trending <br /> TV Show <br /> To Watch Now</h1>
            <p>Most watched tv shows by days</p>
            <div className="brdr w-100"></div>
          </div>
          {Tv.map((tv,index)=><Item key={index} data={tv}/>)}
        </div>
        <div className="row my-3">
          <div className="col-md-4">
            <div className="brdr w-25"></div>
            <h1>Trending <br /> People <br /> To Watch Now</h1>
            <p>Most watched people by days</p>
            <div className="brdr w-100"></div>
          </div>
          {People.map((people,index)=><Item key={index} data={people}/>)}
        </div>
      </div>
    </>:<Loading/>}
  </>
  )
}
