import React, { useEffect, useState } from 'react';
import { getDetails } from '../../Api';
import Loading from '../Loading/Loading';
import { useParams } from 'react-router-dom';

export default function ItemDetails() {

    let params = useParams();

    const [Details , setDetails] = useState('');

    async function getData(){
        let details = await getDetails(params.media,params.id);
        setDetails(details);
    };
    useEffect(() => {
        getData();
    }, []);
    
  return (<>
    {Details?<>
        {Details.profile_path?<>
            <div className="container">
                <div className="row">
                    <div className="col-md-4">
                        <img className='w-100' src={`https://image.tmdb.org/t/p/w500${Details.profile_path}`} alt="image" draggable="false"/>
                    </div>
                    <div className="col-md-8">
                        <h2 className='mt-md-0 mt-3'>{Details.name}</h2>
                        <p>Also known as:</p>
                        {Details.also_known_as.map((value,index)=><span key={index} className='badge bg-info p-2 m-2'>{value}</span>)}
                        <ul className='list-unstyled my-2'>
                            <li className='my-2'>{`Birthday: ${Details.birthday}`}</li>
                            <li className='my-2'>{`Place of birth: ${Details.place_of_birth}`}</li>
                            <li className='my-2'>{`Known for department: ${Details.known_for_department}`}</li>
                            <li className='my-2'>{`Popularity: ${Details.popularity}`}</li>
                        </ul>
                        <p className='text-secondary lead'>{Details.biography.split(' ').slice(0,50).join(' ')}</p>
                    </div>
                </div>
            </div>
        </>:<>
            <div className="container">
                <div className="row">
                    <div className="col-md-4">
                        <img className='w-100' src={`https://image.tmdb.org/t/p/w500${Details.poster_path}`} alt="image" draggable="false"/>
                    </div>
                    <div className="col-md-8">
                        <h2 className='mt-md-0 mt-3'>{Details.title} {Details.name}</h2>
                        {Details.genres.map((value,index)=><span key={index} className='badge bg-info p-2 m-2'>{value.name}</span>)}
                        <ul className='list-unstyled my-2'>
                            <li className='my-2'>{`Vote: ${Details.vote_average.toFixed(1)}`}</li>
                            <li className='my-2'>{`Vote count: ${Details.vote_count}`}</li>
                            <li className='my-2'>{`Popularity: ${Details.popularity}`}</li>
                            <li className='my-2'>{`Release date: ${Details.release_date}`}</li>
                        </ul>
                        <p className='text-secondary lead'>{Details.overview}</p>
                    </div>
                </div>
            </div>
        </>}
    </>:<Loading/>}
  </>
  )
}
