import React, { useState } from 'react';
import { axiosPost } from '../../Api';
import { useNavigate } from 'react-router-dom';
import './Signup.css';
import Joi from 'joi';

export default function Signup() {

  let navigate = useNavigate();
  const [isLoading, setisLoading] = useState(false);
  const [errorMsg, seterrorMsg] = useState('');
  const [errorList, seterrorList] = useState([]);

  const [formData, setFormData] = useState({
    first_name:"",
    last_name:"",
    email:"",
    password:"",
    age:0
  });

  function getFormData({target}) {
    setFormData({
      ...formData,
      [target.name]:target.value
    })
  };

  async function setData(e) {
    setisLoading(true);
    e.preventDefault();
    let validationResult = validateRegisterForm();
    if(validationResult.error){
      setisLoading(false);
      seterrorList(validationResult.error.details);
    }else{
      let result = await axiosPost(formData,'signup');
      if (result.message=='success') {
        navigate('/signin')
      } else{
        setisLoading(false);
        seterrorMsg(result.errors);
      }
    }
  }

  function validateRegisterForm() {
    let scheme = Joi.object({
      first_name:Joi.string().alphanum().pattern(/^[A-Z]/).min(3).max(15).required(),
      last_name:Joi.string().alphanum().pattern(/^[A-Z]/).min(3).max(15).required(),
      age:Joi.number().min(16).max(80).required(),
      email:Joi.string().email({minDomainSegments: 2 , tlds: {allow: ['com' , 'net']}}).required(),
      password:Joi.string().pattern(new RegExp('^[a-zA-Z0-9]{3,30}$')).required()
    });
    return scheme.validate(formData , {abortEarly:false});
  };

  return (<>
    <h1 className='mb-3'>Register Now</h1>
    {errorList.map((err,index)=>{
      if(err.context.label === 'first_name'){
        return <div key={index} className="alert alert-danger">First name must start with a capital letter and from 3 to 8 letters</div>
      }else if(err.context.label === 'last_name'){
        return <div key={index} className="alert alert-danger">Last name must start with a capital letter and from 3 to 8 letters</div>
      }else if(err.context.label === 'password'){
        return <div key={index} className="alert alert-danger">Password must contain (letters and numbers) from 3 to 15</div>
      }else{
        return <div key={index} className="alert alert-danger">{err.message}</div>
      }
    })}
    {errorMsg?
      <div className="alert alert-danger">
        {errorMsg?.email?.message}
      </div>
    :''}
    <form className='myForm d-flex flex-column' onSubmit={setData}>
      <label htmlFor="first_name" className='mt-2 mb-1'>First Name:</label>
      <input onChange={getFormData} type="text" className='form-control mt-1 mb-2' name='first_name' id='first_name'/>
      <label htmlFor="last_name" className='mt-2 mb-1'>Last Name:</label>
      <input onChange={getFormData} type="text" className='form-control mt-1 mb-2' name='last_name' id='last_name'/>
      <label htmlFor="age" className='mt-2 mb-1'>Age:</label>
      <input onChange={getFormData} type="number" className='form-control mt-1 mb-2' name='age' id='age'/>
      <label htmlFor="email" className='mt-2 mb-1'>Email:</label>
      <input onChange={getFormData} type="email" className='form-control mt-1 mb-2' name='email' id='email'/>
      <label htmlFor="password" className='mt-2 mb-1'>Password:</label>
      <input onChange={getFormData} type="password" className='form-control mt-1 mb-3' name='password' id='password'/>
      <button className='btn btn-info text-white mt-4'>{isLoading?<>
        <span>Loading</span>
        <div className="spinner-signup d-inline">
          <div className="bounce1" />
          <div className="bounce2" />
          <div className="bounce3" />
        </div>
      </>:'Register'}
      </button>
    </form>
  </>
  )
}
