import React, { useState } from 'react';
import { axiosPost } from '../../Api';
import { useNavigate } from 'react-router-dom';
import '../Signup/Signup.css';
import Joi from 'joi';

export default function Signin() {

  let navigate = useNavigate()

  const [isLoading, setisLoading] = useState(false)
  const [errorMsg, seterrorMsg] = useState('')
  const [errorList, seterrorList] = useState([]);

  const [formData, setFormData] = useState({
    email:"",
    password:""
  });

  function getFormData({target}) {
    setFormData({
      ...formData,
      [target.name]:target.value
    })
  };

  async function setData(e) {
    setisLoading(true);
    e.preventDefault();
    let validationResult = validateRegisterForm();
    if(validationResult.error){
      setisLoading(false);
      seterrorList(validationResult.error.details);
    }else{
      let result = await axiosPost(formData,'signin');
      if (result.message=='success') {
        localStorage.setItem('token',result.token)
        navigate('/home')
      } else{
        setisLoading(false);
        seterrorMsg(result);
      }
    }
  }

  function validateRegisterForm() {
    let scheme = Joi.object({
      email:Joi.string().email({minDomainSegments: 2 , tlds: {allow: ['com' , 'net']}}).required(),
      password:Joi.string().pattern(new RegExp('^[a-zA-Z0-9]{3,30}$')).required()
    });
    return scheme.validate(formData , {abortEarly:false});
  };

  return (<>
    <h1 className='mb-3'>Login Now</h1>
    {errorList.map((err,index)=>{
      if(err.context.label === 'password'){
        return <div key={index} className="alert alert-danger">Password must contain (letters and numbers) from 3 to 15</div>
      }else{
        return <div key={index} className="alert alert-danger">{err.message}</div>
      }
    })}
    {errorMsg?
      <div className="alert alert-danger">
        {errorMsg?.message}
      </div>
    :''}
    <form className='myForm d-flex flex-column' onSubmit={setData}>
      <label htmlFor="email" className='mt-2 mb-1'>Email:</label>
      <input onChange={getFormData} type="email" className='form-control mt-1 mb-2' name='email' id='email'/>
      <label htmlFor="password" className='mt-2 mb-1'>Password:</label>
      <input onChange={getFormData} type="password" className='form-control mt-1 mb-3' name='password' id='password'/>
      <button className='btn btn-info text-white mt-4'>{isLoading?<>
        <span>Loading</span>
        <div className="spinner-signup d-inline">
          <div className="bounce1" />
          <div className="bounce2" />
          <div className="bounce3" />
        </div>
      </>:'Login'}
      </button>
    </form>
  </>
  )
}
