import './App.css';
import Home from './Components/Home/Home';
import Movies from './Components/Movies/Movies';
import Tv from './Components/Tv/Tv';
import People from './Components/People/People';
import NotFound from './Components/NotFound/NotFound';
import RootLayout from './Components/Layout/RootLayout';
import AuthLayout from './Components/Layout/AuthLayout';
import Signin from './Components/Signin/Signin';
import Signup from './Components/Signup/Signup';
import { createHashRouter,RouterProvider} from 'react-router-dom';
import ItemDetails from './Components/ItemDetails/ItemDetails';
import ProtectedRoutes from './Components/ProtectedRoutes/ProtectedRoutes';

function App() {
  let routes = createHashRouter([
    {path:'/' , element:<RootLayout/> , children:[
      {index:true , element:<ProtectedRoutes><Home/></ProtectedRoutes>},
      {path:'home' , element:<ProtectedRoutes><Home/></ProtectedRoutes>},
      {path:'details/:media/:id' , element:<ProtectedRoutes><ItemDetails/></ProtectedRoutes>},
      {path:'movies' , element:<ProtectedRoutes><Movies/></ProtectedRoutes>},
      {path:'tv' , element:<ProtectedRoutes><Tv/></ProtectedRoutes>},
      {path:'people' , element:<ProtectedRoutes><People/></ProtectedRoutes>},
      {path:'*' , element:<ProtectedRoutes><NotFound/></ProtectedRoutes>}
      ]
    },
    {path:'/' , element:<AuthLayout/> , children:[
      {index:true , element:<Signin/>},
      {path:'signin' , element:<Signin/>},
      {path:'signup' , element:<Signup/>}
      ]
    }
  ])
  return (
    <>
      <RouterProvider router={routes}/>
    </>
  );
}

export default App;
