import React from 'react';
import imgNotFound from '../../Images/404.png';

export default function NotFound() {
  return (<>
      <div className="container">
        <img className='w-25 position-fixed top-50 start-50 translate-middle notFound' src={imgNotFound} alt="image" draggable="false"/>
      </div>
    </>
  )
};
