import Axios from "axios";

export let getTrending = async (mediaType)=>{
    let {data} = await Axios.get(`https://api.themoviedb.org/3/trending/${mediaType}/week?api_key=16542d9f1f371134892646df5c5d7bbf`)
    return data.results
};

export let getDetails = async (mediaType,id)=>{
    let {data} = await Axios.get(`https://api.themoviedb.org/3/${mediaType}/${id}?api_key=16542d9f1f371134892646df5c5d7bbf`)
    return data
};

export let axiosPost = async (formData,endPoint)=>{
    let {data} = await Axios.post(`https://movies-api.routemisr.com/${endPoint}`,formData)
    return data
};