import React from 'react';
import { NavLink } from 'react-router-dom';
import navLogo from '../../Images/logo-dark.webp';

export default function Navbar(props) {
  return (<>
    <nav className="navbar navbar-expand-lg navbar-dark shadow mb-4">
      <div className="container-fluid">
        <NavLink className="navbar-brand pb-2 pt-0" to={'/'}><img src={navLogo} alt="image" draggable="false"/></NavLink>
        <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarNav" aria-controls="navbarNav" aria-expanded="false" aria-label="Toggle navigation">
          <span className="navbar-toggler-icon" />
        </button>
        <div className="collapse navbar-collapse" id="navbarNav">
          {!props.auth?<>
            <ul className="navbar-nav me-auto mb-2 mb-lg-0 text-center">
              <li className="nav-item">
                <NavLink className="nav-link" to={'/home'}>Home</NavLink>
              </li>
              <li className="nav-item">
                <NavLink className="nav-link" to={'/movies'}>Movies</NavLink>
              </li>
              <li className="nav-item">
                <NavLink className="nav-link" to={'/tv'}>Tv Show</NavLink>
              </li>
              <li className="nav-item">
                <NavLink className="nav-link" to={'/people'}>People</NavLink>
              </li>
            </ul>
          </>:''}
          
            {!props.auth?<>
              <ul className="navbar-nav mb-2 mb-lg-0 text-center">
                  <li className="nav-item">
                    <NavLink onClick={props.logout} className="nav-link">Logout</NavLink>
                  </li>
              </ul>
            </>:<>
              <ul className="navbar-nav mb-2 mb-lg-0 text-center ms-auto">
                <li className="nav-item">
                  <NavLink className="nav-link" to={'/signin'}>Login</NavLink>
                </li>
                <li className="nav-item">
                  <NavLink className="nav-link" to={'/signup'}>Register</NavLink>
                </li>
              </ul>
            </>}
        </div>
      </div>
    </nav>

  </>
  )
}
