import React from 'react';
import { Link } from 'react-router-dom';

export default function Item(item) {
  let {id,title,name,poster_path,overview,vote_average,media_type,profile_path,original_name,known_for_department,popularity} = item.data
  return (<>
    {profile_path?<>
      <div className="col-lg-2 col-md-4">
        <div className="item text-center m-2">
          <div className="poster position-relative overflow-hidden">
            <img src={`https://image.tmdb.org/t/p/w500${profile_path}`} className='w-100' alt="image" draggable="false"/>
            <Link to={`/details/${media_type}/${id}`}>
              <div className="overlay position-absolute w-100 h-100 bg-opacity-75 bg-black p-1 d-flex align-items-center justify-content-center">
                <p className='text-light text-opacity-75'></p>
                <div className='text-light text-opacity-75'>
                  {original_name} <br /> <br /> Department: {known_for_department} <br /> <br /> Popularity: {popularity}
                </div>
              </div>
            </Link>
          </div>
          <h6 className='text-white my-2'>{name}</h6>
        </div>
      </div>
    </>:<>
      <div className="col-lg-2 col-md-4">
        <div className="item text-center m-2">
          <div className="poster position-relative overflow-hidden">
            <img src={`https://image.tmdb.org/t/p/w500${poster_path}`} className='w-100' alt="image" draggable="false"/>
            <div className="vote position-absolute top-0 end-0 p-1">
              <p className='p-2'>{vote_average.toFixed(1)}</p>
            </div>
            <Link to={`/details/${media_type}/${id}`}>
              <div className="overlay position-absolute w-100 h-100 bg-opacity-75 bg-black p-1 d-flex align-items-center justify-content-center">
                <p className='text-light text-opacity-75'>{overview.split(' ').slice(0,15).join(' ')}</p>
              </div>
            </Link>
          </div>
          {title?<h6 className='text-white my-2'>{title}</h6>:<h6 className='text-white my-2'>{name}</h6>}
        </div>
      </div>
    </>}
  </>
  )
}
